<script>
import DropzoneFileUpload from "@/tkViews/components/Inputs/DropzoneFileUpload.vue";
import buttonWithLoading from "@/tkViews/components/layout/ButtonWithLoading";
export default {
  components: {
    DropzoneFileUpload,
    buttonWithLoading,
  },
  props: {
    data: {
      type: Object,
    },
  },
  data() {
    return {
      fileSingle: [],
      tutorial: {
        user: {
          userId: "",
          name: "Wayne",
          email: "mailwpw@gmail.com",
          phone: "+1 400 820 8820",
          website: "",
        },
        teacherInfo: {
          userId: "",
          studioId: "",
          address: "",
        },
        studioInfo: {
          id: "",
          name: "",
          storefrontColor: "",
        },
      },
      storeFrontIsCreate: false,
      instruments: [],
      selectedInstruments: []
    };
  },
  watch: {
    fileSingle(newVal, oldVal) {
      setTimeout(() => {
        this.tutorial.logo = newVal[0].dataURL;
      }, 200);
    },
  },
  created() {
    let self = this
    self.instruments = self.$tools.instruments
  },
  mounted() {
    this.tutorial = this.data;
    // console.log(this.tutorial);
  },
  methods: {
    createStorefront() {
      let self = this
      setTimeout(() => {
        self.$notify({
          message: 'Success',
          type: 'success',
          timeout: 2000
        })
        self.storeFrontIsCreate = true
      }, 2000)
    },
    startLessonType() {
      this.$emit("startLessonType")
    },
    selectInstrument(item, index) {
      let self = this
      let i = self.selectedInstruments.indexOf(index)
      if (i > -1) {
        self.selectedInstruments.splice(i, 1)
      }else {
        self.selectedInstruments.push(index)
      }
    }
  },
};
</script>